// The initialize function must be run each time a new page is loaded
Office.initialize = () => {
  document.getElementById("sideload-msg").style.display = "none";
  document.getElementById("app-body").style.display = "flex";
  document.getElementById("run").onclick = run;
  makeTable();
  dataValidate();
  showTaskpane(); 
};

//A Blob of Experimental Stuff TO DO 
async function makeTable() {
  try {
    await Excel.run(async context => {
    //First set up a table and only then show the taskpane 
    
    var currentWorksheet = context.workbook.worksheets.getActiveWorksheet();
    var expensesTable = currentWorksheet.tables.add("A1:D1", true);
    expensesTable.name = "ExpensesTable";

    expensesTable.getHeaderRowRange().values =
    [["Date", "Merchant", "Category", "Amount"]];

    expensesTable.rows.add(null, [
        ["1/1/2017", "The Phone Company", "Communications", "120"],
        ["1/2/2017", "Northwind Electric Cars", "Transportation", "142.33"],
        ["1/5/2017", "Best For You Organics Company", "Groceries", "27.9"],
        ["1/10/2017", "Coho Vineyard", "Restaurant", "33"],
        ["1/11/2017", "Bellows College", "Education", "350.1"],
        ["1/15/2017", "Trey Research", "Other", "135"],
        ["1/15/2017", "Best For You Organics Company", "Groceries", "97.88"]
    ])
  })
 } catch(error) {
    console.error(error);
  }
}
      
async function dataValidate() {
  try {
    await Excel.run(async context => {
    var sheet = context.workbook.worksheets.getActiveWorksheet();
    var range = sheet.getRange("D3:D8");

    range.dataValidation.rule = {
            wholeNumber: {
                formula1: 0,
                operator: "GreaterThan"
            }
        };

    return context.sync();
  })
  } catch(error) {
    console.error(error); 
  }
} 

function showTaskpane() {
  Office.addin.showAsTaskpane();
}